'use client';

import {
  createContext,
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useState,
  type ReactNode,
} from 'react';
import { APP_NAME } from '@/config/appConfig';

type LegalModalType = 'terms' | 'privacy' | 'lgpd' | null;

interface LegalModalContextValue {
  openModal: (type: Exclude<LegalModalType, null>) => void;
  closeModal: () => void;
}

const LegalModalContext = createContext<LegalModalContextValue | null>(null);

interface LegalModalProviderProps {
  children: ReactNode;
}

export function LegalModalProvider({ children }: LegalModalProviderProps) {
  const [activeModal, setActiveModal] = useState<LegalModalType>(null);

  const closeModal = useCallback(() => setActiveModal(null), []);
  const openModal = useCallback(
    (type: Exclude<LegalModalType, null>) => setActiveModal(type),
    []
  );

  useEffect(() => {
    if (!activeModal) return;

    const handleKeyDown = (event: KeyboardEvent) => {
      if (event.key === 'Escape') {
        closeModal();
      }
    };

    document.addEventListener('keydown', handleKeyDown);
    document.body.style.overflow = 'hidden';

    return () => {
      document.removeEventListener('keydown', handleKeyDown);
      document.body.style.overflow = '';
    };
  }, [activeModal, closeModal]);

  const modalContent = useMemo(() => {
    const paragraphs = {
      terms: (
        <div className="space-y-4 text-sm text-gray-600 dark:text-gray-300">
          <p>
            Seja bem-vindo(a) ao <strong>{APP_NAME}</strong>. Ao utilizar a
            plataforma você declara ter lido, entendido e aceito estes Termos de
            Uso. Caso não concorde com algum ponto, recomendamos não prosseguir
            com o uso do serviço.
          </p>
          <h3 className="text-base font-semibold text-gray-900 dark:text-white">
            1. Objetivo do serviço
          </h3>
          <p>
            O {APP_NAME} é uma solução de gestão de cobranças que permite a
            criação, o envio e o monitoramento de cobranças recorrentes, com
            integrações a gateways de pagamento e envio de notificações via
            WhatsApp. Nosso foco é fornecer as ferramentas necessárias para
            automatizar e organizar o ciclo de cobrança da sua empresa.
          </p>
          <h3 className="text-base font-semibold text-gray-900 dark:text-white">
            2. Cadastro e responsabilidades
          </h3>
          <p>
            Você se compromete a fornecer informações verdadeiras e completas no
            momento do cadastro, bem como mantê-las atualizadas. O acesso à
            plataforma é pessoal e intransferível; portanto, manter suas
            credenciais em sigilo é responsabilidade do usuário. Qualquer ação
            realizada com as credenciais cadastradas será atribuída ao titular
            da conta.
          </p>
          <h3 className="text-base font-semibold text-gray-900 dark:text-white">
            3. Uso adequado
          </h3>
          <p>
            É proibido utilizar o {APP_NAME} para fins ilegais, para o envio de
            comunicações não solicitadas (SPAM) ou para atividades que possam
            violar direitos de terceiros. Reservamo-nos o direito de suspender
            ou cancelar contas que desrespeitem estes Termos ou leis vigentes.
          </p>
          <h3 className="text-base font-semibold text-gray-900 dark:text-white">
            4. Integrações de pagamento
          </h3>
          <p>
            A plataforma integra-se a diferentes provedores de pagamento para
            emissão de PIX e boletos. Cada provedor possui termos próprios, que
            devem ser observados e aceitos diretamente pelo usuário. O{' '}
            {APP_NAME} não coleta, armazena ou manipula dados sensíveis de
            pagamento para fins distintos daqueles necessários ao funcionamento
            das integrações.
          </p>
          <h3 className="text-base font-semibold text-gray-900 dark:text-white">
            5. Disponibilidade e suporte
          </h3>
          <p>
            Empregamos boas práticas para manter a plataforma disponível e
            segura. No entanto, podem ocorrer interrupções esporádicas para
            manutenção ou por fatores externos. Oferecemos suporte pelos canais
            disponibilizados na aplicação, buscando atender as solicitações com
            brevidade.
          </p>
          <h3 className="text-base font-semibold text-gray-900 dark:text-white">
            6. Alterações
          </h3>
          <p>
            Estes Termos podem ser atualizados a qualquer momento para refletir
            melhorias do serviço, mudanças legislativas ou ajustes operacionais.
            Sempre que houver alteração relevante, notificaremos os usuários por
            meio dos canais informados no cadastro ou por avisos na própria
            plataforma.
          </p>
        </div>
      ),
      privacy: (
        <div className="space-y-4 text-sm text-gray-600 dark:text-gray-300">
          <p>
            A política de privacidade do <strong>{APP_NAME}</strong> foi criada
            para explicar como lidamos com as informações fornecidas por você e
            por seus clientes. Nosso compromisso é utilizar os dados apenas para
            operar e melhorar a plataforma, respeitando a legislação vigente,
            especialmente a Lei Geral de Proteção de Dados (LGPD).
          </p>
          <h3 className="text-base font-semibold text-gray-900 dark:text-white">
            1. Coleta de informações
          </h3>
          <p>
            Coletamos apenas os dados necessários para o funcionamento da
            plataforma: informações cadastrais dos usuários e das empresas,
            dados de clientes para fins de cobrança, configurações de gateways e
            histórico de interações dentro do sistema.
          </p>
          <h3 className="text-base font-semibold text-gray-900 dark:text-white">
            2. Uso dos dados
          </h3>
          <p>
            Utilizamos os dados exclusivamente para prestar o serviço contratado
            (emissão de cobranças, envio de notificações, relatórios,
            automatizações e suporte). Não comercializamos, compartilhamos ou
            utilizamos as informações para fins externos ao {APP_NAME}.
          </p>
          <h3 className="text-base font-semibold text-gray-900 dark:text-white">
            3. Compartilhamento
          </h3>
          <p>
            Os dados podem ser compartilhados apenas com provedores
            necessários ao funcionamento da plataforma (por exemplo, gateways de
            pagamento ou serviços de envio de mensagens), sempre sob contratos
            que resguardam a confidencialidade e a finalidade exclusiva de
            prestação do serviço.
          </p>
          <h3 className="text-base font-semibold text-gray-900 dark:text-white">
            4. Segurança
          </h3>
          <p>
            Adotamos medidas técnicas e administrativas de segurança compatíveis
            com o porte do serviço, buscando proteger os dados contra acessos não
            autorizados, vazamentos ou uso indevido. Ainda assim, recomendamos
            que os usuários adotem boas práticas, como uso de senhas fortes e
            sigilo das credenciais.
          </p>
          <h3 className="text-base font-semibold text-gray-900 dark:text-white">
            5. Retenção e exclusão
          </h3>
          <p>
            Os dados são mantidos enquanto a conta estiver ativa ou pelo tempo
            necessário para cumprir obrigações legais e contratuais. Mediante
            solicitação, o titular poderá requerer a exclusão ou anonimização de
            registros, respeitadas as limitações legais.
          </p>
        </div>
      ),
      lgpd: (
        <div className="space-y-4 text-sm text-gray-600 dark:text-gray-300">
          <p>
            O <strong>{APP_NAME}</strong> observa os princípios e as bases legais
            da Lei Geral de Proteção de Dados (Lei nº 13.709/2018). Atuamos como
            operador e/ou controlador de dados conforme o fluxo de uso de cada
            cliente, sempre limitando o tratamento ao mínimo necessário para
            executar as funcionalidades contratadas.
          </p>
          <h3 className="text-base font-semibold text-gray-900 dark:text-white">
            1. Bases legais
          </h3>
          <p>
            Tratamos dados pessoais com base em fundamentos como execução de
            contrato, cumprimento de obrigação legal/regulatória ou legítimo
            interesse, sempre observando o equilíbrio entre a finalidade do
            tratamento e os direitos dos titulares.
          </p>
          <h3 className="text-base font-semibold text-gray-900 dark:text-white">
            2. Direitos dos titulares
          </h3>
          <p>
            Os titulares dos dados podem solicitar, a qualquer momento:
            confirmação de tratamento, acesso, correção, anonimização, portabilidade,
            informação sobre compartilhamento, revogação de consentimentos e eliminação
            de dados tratados em desacordo com a lei.
          </p>
          <h3 className="text-base font-semibold text-gray-900 dark:text-white">
            3. Encarregado
          </h3>
          <p>
            Para questões relacionadas à privacidade, utilize os canais oficiais
            do {APP_NAME}. As solicitações serão analisadas em conformidade com a LGPD
            e respondidas dentro dos prazos legais.
          </p>
          <h3 className="text-base font-semibold text-gray-900 dark:text-white">
            4. Responsabilidades dos clientes
          </h3>
          <p>
            Os clientes do {APP_NAME} são responsáveis por garantir que dados
            pessoais inseridos na plataforma tenham sido coletados de forma
            adequada, com base legal e observando os direitos dos titulares,
            especialmente em comunicações com consumidores finais.
          </p>
        </div>
      ),
    };

    if (!activeModal) return null;

    const titleMap: Record<Exclude<LegalModalType, null>, string> = {
      terms: 'Termos de Uso',
      privacy: 'Política de Privacidade',
      lgpd: 'LGPD e Proteção de Dados',
    };

    return (
      <div
        className="fixed inset-0 z-[999] flex items-center justify-center bg-black/60 px-4 backdrop-blur-sm"
        role="dialog"
        aria-modal="true"
        aria-labelledby="legal-modal-title"
        onClick={closeModal}
      >
        <div
          className="max-w-3xl w-full max-h-[85vh] overflow-hidden rounded-2xl bg-white dark:bg-gray-900 border border-gray-200 dark:border-gray-700 shadow-2xl"
          onClick={(event) => event.stopPropagation()}
        >
          <div className="flex items-center justify-between px-6 py-4 border-b border-gray-200 dark:border-gray-800">
            <h2
              id="legal-modal-title"
              className="text-xl font-semibold text-gray-900 dark:text-white"
            >
              {titleMap[activeModal]}
            </h2>
            <button
              onClick={closeModal}
              className="rounded-full p-2 text-gray-500 hover:text-gray-900 hover:bg-gray-100 dark:hover:bg-gray-800 dark:text-gray-400"
              aria-label="Fechar"
            >
              <svg
                className="w-5 h-5"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
                strokeWidth={2}
                strokeLinecap="round"
                strokeLinejoin="round"
              >
                <path d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>
          <div className="px-6 py-5 overflow-y-auto max-h-[70vh]">
            {paragraphs[activeModal]}
          </div>
          <div className="px-6 py-4 bg-gray-50 dark:bg-gray-800 border-t border-gray-200 dark:border-gray-800 text-right">
            <button
              onClick={closeModal}
              className="inline-flex items-center px-4 py-2 rounded-lg bg-blue-600 text-white font-semibold hover:bg-blue-700 transition-colors"
            >
              Entendi
            </button>
          </div>
        </div>
      </div>
    );
  }, [activeModal, closeModal]);

  return (
    <LegalModalContext.Provider value={{ openModal, closeModal }}>
      {children}
      {modalContent}
    </LegalModalContext.Provider>
  );
}

export function useLegalModal() {
  const context = useContext(LegalModalContext);

  if (!context) {
    throw new Error(
      'useLegalModal deve ser utilizado dentro de um LegalModalProvider'
    );
  }

  return context;
}

