"use client";
import Checkbox from "@/components/form/input/Checkbox";
import Input from "@/components/form/input/InputField";
import Label from "@/components/form/Label";
import { ChevronLeftIcon, EyeCloseIcon, EyeIcon } from "@/icons";
import Link from "next/link";
import { useRouter } from 'next/navigation';
import React, { useState } from "react";
import { services } from '@/services/api';
import { toast } from 'react-hot-toast';
import { useLegalModal } from '@/components/legal/LegalModalProvider';

export default function SignUpForm() {
  const router = useRouter();
  const { openModal } = useLegalModal();
  const [showPassword, setShowPassword] = useState(false);
  const [isChecked, setIsChecked] = useState(false);
  const [loading, setLoading] = useState(false);
  const [formData, setFormData] = useState({
    nome: '',
    email: '',
    senha: '',
    celular: '',
  });
  const [fieldErrors, setFieldErrors] = useState<Record<string, string>>({});

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!isChecked) {
      toast.error('Você precisa aceitar os termos e condições');
      return;
    }

    // Limpar erros anteriores
    setFieldErrors({});
    setLoading(true);

    try {
      const body = {
        ...formData,
        role: 'ADMIN', // Definindo o papel como ADMIN
      }
      await services.auth.register(body);
      
      toast.success('Registro realizado com sucesso! Faça login para continuar.');
      router.push('/auth/login');
    } catch (error: any) {
      // Extrair erros de validação dos detalhes
      if (error.response?.data?.details && Array.isArray(error.response.data.details)) {
        const errors: Record<string, string> = {};
        error.response.data.details.forEach((detail: { field: string; message: string }) => {
          // Remover o prefixo "body." do campo
          const fieldName = detail.field.replace('body.', '');
          errors[fieldName] = detail.message;
        });
        setFieldErrors(errors);
      }
      
      const errorMessage = error.response?.data?.message || error.response?.data?.error || 'Erro ao fazer registro';
      toast.error(errorMessage);
      console.log('error:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (field: string, value: string) => {
    setFormData({ ...formData, [field]: value });
    // Limpar erro do campo quando o usuário começar a digitar
    if (fieldErrors[field]) {
      setFieldErrors({ ...fieldErrors, [field]: '' });
    }
  };
  return (
    <div className="flex flex-col flex-1 lg:w-1/2 w-full overflow-y-auto no-scrollbar">
      <div className="w-full max-w-md sm:pt-10 mx-auto mb-5">
        <Link
          href="/"
          className="inline-flex items-center text-sm text-gray-500 transition-colors hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-300"
        >
          <ChevronLeftIcon />
                        Voltar para o início
        </Link>
      </div>
      <div className="flex flex-col justify-center flex-1 w-full max-w-md mx-auto">
        <div>
          <div className="mb-5 sm:mb-8">
            <h1 className="mb-2 font-semibold text-gray-800 text-title-sm dark:text-white/90 sm:text-title-md">
              Criar Conta
            </h1>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Preencha os dados abaixo para criar sua conta!
            </p>
          </div>
          <div>
            <form onSubmit={handleSubmit}>
              <div className="space-y-5">
                <div>
                  <Label>
                    Nome Completo<span className="text-error-500">*</span>
                  </Label>
                  <Input
                    type="text"
                    placeholder="Digite seu nome completo"
                    value={formData.nome}
                    onChange={(e) => handleInputChange('nome', e.target.value)}
                    error={fieldErrors.nome || false}
                    required
                  />
                </div>

                <div>
                  <Label>
                    Email<span className="text-error-500">*</span>
                  </Label>
                  <Input
                    type="email"
                    placeholder="Digite seu email"
                    value={formData.email}
                    onChange={(e) => handleInputChange('email', e.target.value)}
                    error={fieldErrors.email || false}
                    required
                  />
                </div>

                <div>
                  <Label>
                    Celular<span className="text-error-500">*</span>
                  </Label>
                  <Input
                    type="tel"
                    placeholder="Digite seu celular"
                    value={formData.celular}
                    onChange={(e) => handleInputChange('celular', e.target.value)}
                    error={fieldErrors.celular || false}
                    required
                  />
                </div>

                <div>
                  <Label>
                    Senha<span className="text-error-500">*</span>
                  </Label>
                  <div className="relative">
                    <Input
                      placeholder="Digite sua senha"
                      type={showPassword ? "text" : "password"}
                      value={formData.senha}
                      onChange={(e) => handleInputChange('senha', e.target.value)}
                      error={!!fieldErrors.senha}
                      required
                    />
                    <span
                      onClick={() => setShowPassword(!showPassword)}
                      className="absolute z-30 -translate-y-1/2 cursor-pointer right-4 top-1/2"
                    >
                      {showPassword ? (
                        <EyeIcon className="fill-gray-500 dark:fill-gray-400" />
                      ) : (
                        <EyeCloseIcon className="fill-gray-500 dark:fill-gray-400" />
                      )}
                    </span>
                  </div>
                  {fieldErrors.senha && (
                    <p className="mt-1 text-xs text-error-500">{fieldErrors.senha}</p>
                  )}
                </div>

                <div className="flex items-center gap-3">
                  <Checkbox
                    className="w-5 h-5"
                    checked={isChecked}
                    onChange={setIsChecked}
                  />
                  <p className="inline-block font-normal text-gray-500 dark:text-gray-400">
                                    Eu concordo com a{" "}
                    <span className="text-gray-800 dark:text-white/90 cursor-pointer" onClick={() => openModal('terms')}>
                      Termos e Condições
                    </span>{" "}
                    e nossa{" "}
                    <span className="text-gray-800 dark:text-white cursor-pointer" onClick={() => openModal('privacy')}>
                      Política de Privacidade
                    </span>
                  </p>
                </div>

                <div>
                  <button 
                    type="submit"
                    disabled={loading}
                    className={`flex items-center justify-center w-full px-4 py-3 text-sm font-medium text-white transition rounded-lg bg-brand-500 shadow-theme-xs hover:bg-brand-600 ${
                      loading ? 'opacity-50' : ''
                    }`}
                  >
                    {loading ? 'Registrando...' : 'Criar Conta'}
                  </button>
                </div>
              </div>
            </form>

            <div className="mt-5">
              <p className="text-sm font-normal text-center text-gray-700 dark:text-gray-400 sm:text-start">
                            Já tem uma conta?{" "}
                <Link
                  href="/auth/login"
                  className="text-brand-500 hover:text-brand-600 dark:text-brand-400"
                >
                                Entrar
                </Link>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
